/**
 * Modal Component
 * --------------------------------
 * A simple inline modal for opening boxes.
 */
.modal {
  display: none;
  float: left;
  width: 100%;
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 10000;
  text-align: center;
  opacity: 0;
  overflow: scroll;
  -webkit-overflow-scrolling: touch; }
  .modal .modal-content-wrap {
    background-color: white;
    position: relative;
    top: 0px;
    opacity: 0;
    float: left;
    width: 100%; }
  .modal .modal-close {
    position: absolute;
    color: white;
    transition: all;
    opacity: 1;
    top: 1em;
    right: 1em; }
    .modal .modal-close:hover {
      opacity: 0.5; }
  .modal .modal-back-button {
    margin-bottom: -4em; }
  .modal.open {
    display: block;
    opacity: 1;
    animation-name: modal_display_background;
    animation-duration: 500ms;
    animation-timing-function: ease; }
    .modal.open .modal-content-wrap {
      opacity: 1;
      animation-name: modal_display_content;
      animation-duration: 500ms;
      animation-timing-function: ease; }
  .modal.closing {
    opacity: 0;
    animation-name: modal_close_background;
    animation-duration: 300ms;
    animation-timing-function: ease; }
    .modal.closing .modal-content-wrap {
      opacity: 0;
      animation-name: modal_close_content;
      animation-duration: 300ms;
      animation-timing-function: ease; }

.modal .modal-content-wrap {
  width: 80%;
  margin-left: 10%;
  margin-right: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0%; }
  .modal .modal-content-wrap img {
    width: 100%; }

.modal h3 {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  float: left;
  width: 100%; }

.modal .campaign-point-image {
  max-width: 130px;
  margin: 0 auto; }
  .modal .campaign-point-image img {
    max-width: 100%;
    height: auto; }

.modal .campaign-point {
  margin-bottom: 2em; }

.modal .modal-back-button {
  float: left;
  width: 100%; }

.modal.modal-full-width .modal-content-wrap {
  width: 100%;
  margin-left: auto; }

.modal.video-modal .modal-content-wrap {
  background-color: transparent; }
  .modal.video-modal .modal-content-wrap .modal-close {
    z-index: 20;
    color: white;
    right: -1.5em;
    top: -1.5em; }

@keyframes modal_display_background {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes modal_display_content {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes modal_close_background {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes modal_close_content {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

